<template>
  <div>
    <el-form :inline="true" :model="params" class="demo-form-inline">
      <el-form-item label="财务组织" size="mini">
        <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable maxlength="100">
          <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
            <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="业务日期" size="mini">
        <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="单据号" size="mini">
        <el-input v-model="params.deposits_sn" maxlength="100" placeholder="请输入"></el-input>
      </el-form-item>

      <el-form-item label="部门" size="mini">
        <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择" filterable maxlength="100">
          <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="客户名称" size="mini">
        <!-- <el-select clearable v-model="params.customer_id" @focus="SelesTomertion" filterable placeholder="请选择">
          <el-option v-for="item in SelesTomer" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id" style="max-width:400px;">
            <span style="float: left">{{ item.customer_name }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.address }}</span>
          </el-option>
        </el-select> -->
        <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="getCustomerChange" :loading="loading" remote :remote-method="clienteleListInvok">
          <ul class="customer customer-ul4 customer-ul2">
            <li>客户编号</li>
            <li>客户名称</li>
            <li>联系人</li>
            <li>联系电话</li>
            <li>区域</li>
          </ul>
          <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
            <ul class="customer customer-ul4">
              <li :title="item.customer_id">{{ item.customer_id }}</li>
              <li :title="item.customer_name">{{ item.customer_name }}</li>
              <li :title="item.contact_name">{{item.contact_name}}</li>
              <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
              <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
            </ul>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="业务员" size="mini">
        {{(function(){if(params.business_id == 0)params.business_id = null})()}}
        <el-select clearable v-model="params.employee_id" @focus="EmployeeListInvok" @change="employeeChange" filterable>
          <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="审核状态" size="mini">
        <el-select clearable v-model="params.auditor_status">
          <el-option label="全部" :value="0">全部</el-option>
          <el-option label="未审核" :value="1">未审核</el-option>
          <el-option label="已审核" :value="2">已审核</el-option>
          <el-option label="作废" :value="3">作废</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="备注" size="mini">
          <el-input v-model="params.remark" clearable placeholder="请输入备注" @keyup.enter.native='onSearch'></el-input>
        </el-form-item>
      <el-form-item size="mini" class="search-style search">
        <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
        <el-button plain @click="onReset" size="mini">重置</el-button>
      </el-form-item>
    </el-form>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" class="addBtn" size="mini" @click="addFun" v-if="authority.b_add==1">新增</el-button>
      <!---->
      <el-button plain icon="el-icon-delete" size="mini" @click="delFun" v-if="authority.b_del==1">删除</el-button>
      <!---->
      <el-button class="iconfont icon-pass" type="primary" size="mini" @click="examineFun" v-if="authority.b_pass==1">审核</el-button>
      <!---->
      <el-button plain class="iconfont icon-zuofei" size="mini" @click="abolishFun" v-if="authority.b_invalid==1">作废</el-button>
      <el-button plain class="iconfont icon-fanshenhe" size="mini" @click="nopass" v-if="authority.b_nopass==1">弃审</el-button>
      <!---->
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" @selection-change="handleSelectionChange" max-height="600" style="width: 100%">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" @selection-change="handleSelectionChange">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="registe_time" width="100" label="操作" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small" v-if="authority.b_info==1">查看</el-button>
            <!---->
            <el-button @click="updateClick(scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">修改</el-button>
            <!-- -->
            <el-button disabled type="text" size="small" v-else>
              修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="财务组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="deposits_sn" width="180" label="单据号"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="total_price" width="180" label="冲销金额" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.total_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_name" width="180" label="业务员"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="180" label="创建人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="业务日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" width="180" label="单据时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="180" label="审核状态"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_user_name" width="180" label="审核人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="180" label="审核时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_user_name" width="180" label="修改人"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" width="180" label="修改时间"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" width="180" label="备注"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {
  getData,
  opthionList,
  clienteleListCommon
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customePrepaidhandleDefault",
  data() {
    return {
      authority: {},
      loading: false,
      timeValue: [],
      SelesTomer: [], //客户列表
      multipleSelection: []
    };
  },
  computed: {
    ...mapGetters(["depositsIndexACT_showGet"])
  },
  methods: {
    ...mapActions([
      "depositsAdd_show",
      "depositsSearch_show",
      "depositsIndexACT"
    ]),
    getCustomerListRemote(query) {
      //客户列表的模糊查询
      getCustomerList(this, {
        company_id: this.params.company_id,
        customer_name: query
      });
    },
    SelesTomertion() {
      clienteleListCommon(this);
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    getCustomerChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    employeeChange(val) {
      if (val) {
        this.params.employee_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    companyChange(val) {
      if (val != "") {
        if (val) {
          this.params.company_name = this.companyList.filter(
            obj => obj.company_id === val
          )[0].company_name;
        }
      } else {
        if (val) {
          this.params.company_name = this.companyList.filter(
            obj => obj.company_id === val
          )[0].company_name;
        }
      }
      this.$set(this.params, "employee_id", "");
      this.$set(this.params, "department_id", "");
      this.$set(this.params, "customer_id", "");
    },
    onSearch() {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = "";
        this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.depositsIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      getData(this.searchParams);
    },
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getDepositsList", //接口名
        vm: this //传递的this
      });
      this.onSearch();
    },
    addFun() {
      //新增
      this.depositsAdd_show({
        // commont: "depositsAdd",
        addORchange: "add",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/deposits/depositsAdd"
      });
    },
    handleClick(row) {
      //查看
      this.depositsSearch_show({
        commont: "depositsSearch",
        deposits_id: row.deposits_id,
        changePage: "prepaidhandle",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/deposits/depositsSearch"
      });
    },
    updateClick(row) {
      //修改
      this.depositsAdd_show({
        // commont: "depositsAdd",
        deposits_id: row.deposits_id,
        addORchange: "change",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/custom/deposits/depositsAdd"
      });
    },
    delFun() {
      //删除
      var deledata = {
        deleID: "deposits_id",
        deleSelect: this.multipleSelection,
        deleApi: "delDeposits",
        list: {
          UrlApi: "getDepositsList",
          data: this.TableData,
          esData: { page: this.goPage }
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    nopass() {
      //弃审
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].deposits_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "deposits_id",
        eachData: {
          deposits_id: rebateArr
        },
        examineApi: "noauditorPassdeposits",
        list: {
          UrlApi: "getDepositsList",
          data: this.TableData,
          esData: {
            page: this.goPage
          }
        }
      };
      this.$common.examineFun(funcan);
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].deposits_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "deposits_id",
        eachData: {
          deposits_id: rebateArr
        },
        examineApi: "auditorPassdeposits",
        list: {
          UrlApi: "getDepositsList",
          data: this.TableData,
          esData: {
            page: this.goPage
          }
        }
      };
      this.$common.examineFun(funcan);
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].deposits_id);
      }
      var rebateArr = addr.toString();
      var funcan = {
        examineID: "deposits_id",
        eachData: {
          deposits_id: rebateArr
        },
        examineApi: "auditorInvaliddeposits",
        list: {
          UrlApi: "getDepositsList",
          data: this.TableData,
          esData: {
            page: this.goPage
          }
        }
      };
      this.$common.examineFun(funcan);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (this.depositsIndexACT_showGet && this.depositsIndexACT_showGet.params) {
      if (this.depositsIndexACT_showGet.params.start_time) {
        const { start_time, end_time } = this.depositsIndexACT_showGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      this.$set(this, "params", this.depositsIndexACT_showGet.params);
      opthionList(this, this.depositsIndexACT_showGet.params);
    }
    if (this.depositsIndexACT_showGet && this.depositsIndexACT_showGet.goPage) {
      this.params.page = this.depositsIndexACT_showGet.goPage;
      this.params.per_page = this.depositsIndexACT_showGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getDepositsList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>

